.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  height: 100px;
  margin: 0 auto;

  &.sticky{
    background: #FFF;
    position: fixed;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    box-shadow: 0 5px 10px rgba(0 ,0 ,0 ,.1);
    height: 70px;
    z-index: 100;
    & a {
      color: #000;
    }
  }

  & .logo{
    height: 80%;
  }

  & div{
    display: flex;
  }

  .lang{
    color: #fff;
    &:hover{
      color: $primaryColor;
    }
  }

  & ul{
    display: flex;
    margin-right: 30px;
    @media (max-width: 678px){
      display: none;
    }
  }
  &__el{
    margin: 0 15px;
    &:last-child{
      margin: 0 0 0 15px;
    }

    & a {
      color: #fff;
      text-transform: uppercase;
      position: relative;
      &:hover{
        color: $primaryColor;
      }

    //   &::after{
